import React from "react";
import ReactDOM from "react-dom";
import { RocketCntxt } from "./types";
import { HashRouter, Route } from "react-router-dom";
import { initializeIcons } from "@uifabric/icons";
import { CSSTransition } from "react-transition-group";
import Home from "./Home";
import RocketDetails from "./RocketDetails";
import NavHeader from "./NavHeader";
import useRocketFetch from "./useRocketFetch";
import createCtx from "./utils/createCtx";
import "./styles.css";

initializeIcons();

interface Props {
  baseUrl: string;
}

export const [useRocketContext, RocketsProvider] = createCtx<RocketCntxt>();
function App({ baseUrl }: Props) {
  const [rockets, setRockets] = useRocketFetch(10);
  const routes = [
    { path: `/`, name: "home", Component: Home },
    { path: `/:rocketId`, name: "details", Component: RocketDetails }
  ];
  return (
    <RocketsProvider value={{ rockets, setRockets }}>
      <NavHeader />
      {routes.map(({ path, Component, name }) => (
        <Route key={path} exact path={path}>
          {({ match }) => (
            <CSSTransition
              in={!!(match && match.isExact)}
              timeout={300}
              classNames="page"
              unmountOnExit
            >
              <div className={`page ${name}`}>
                <Component baseUrl={baseUrl} />
              </div>
            </CSSTransition>
          )}
        </Route>
      ))}
    </RocketsProvider>
  );
}

const rootElement = document.getElementById("root");
const baseUrl = "";
ReactDOM.render(
  <HashRouter basename={baseUrl}>
    <App baseUrl={baseUrl} />
  </HashRouter>,
  rootElement
);
