import React from "react";
import { Stack, Text } from "office-ui-fabric-react";
import { Link, useRouteMatch } from "react-router-dom";
import { TransitionGroup, CSSTransition } from "react-transition-group";
import Card from "./Card";
import numberWithCommas from "./utils/numberWithCommas";
import { useRocketContext } from "./index";

export default function Home({ baseUrl }: { baseUrl: string }) {
  const { rockets } = useRocketContext();
  return (
    <>
      <Text
        block
        styles={{ root: { color: "rgb(50, 49, 48)", padding: "0px 0 20px 0" } }}
        variant="xxLarge"
      >
        SpaceX Rockets
      </Text>
      <Stack tokens={{ childrenGap: 15 }} verticalAlign="stretch">
        <TransitionGroup component={null}>
          {rockets &&
            rockets.map(
              (
                { id, name, description, firstFlight, imgSrc, costsPerLaunch },
                index
              ) => {
                const delay = Math.max(0, index * 100);
                return (
                  <CSSTransition
                    key={id}
                    classNames="card"
                    timeout={300}
                    unmountOnExit
                  >
                    <Link
                      to={`${baseUrl}/${id}`}
                      style={{ transitionDelay: `${delay}ms` }}
                    >
                      <Card
                        description={description}
                        personaTitle={name}
                        personaSubtitle={`First Flight: ${firstFlight}`}
                        headline={`${numberWithCommas(
                          costsPerLaunch
                        )}$ per launch`}
                        imgSrc={imgSrc}
                      />
                    </Link>
                  </CSSTransition>
                );
              }
            )}
        </TransitionGroup>
      </Stack>
    </>
  );
}
