import { useEffect, useState } from "react";
import { Rocket } from "./types";
export default function useRocketFetch(numberOfRockets: number) {
  const [rockets, setRockets] = useState<Rocket[] | null>(null);
  useEffect(() => {
    fetch(`https://api.spacexdata.com/v3/rockets?limit=${numberOfRockets}`)
      .then(res => res.json())
      .then(data => {
        setRockets(
          data.map((rocket: any) => ({
            id: rocket.rocket_id,
            name: rocket.rocket_name,
            description: rocket.description,
            firstFlight: rocket.first_flight,
            costsPerLaunch: rocket.cost_per_launch,
            imgSrc:
              Array.isArray(rocket.flickr_images) && rocket.flickr_images[0]
          }))
        );
      });
  }, [numberOfRockets]);
  return [rockets, setRockets] as const;
}
