import React from "react";
import { Link as RouterLink, useRouteMatch } from "react-router-dom";
import {
  Text,
  Link,
  Image,
  IconButton,
  ImageFit,
  FontWeights,
  Stack,
  ITextStyles
} from "office-ui-fabric-react";
import styled from "styled-components";
import { useRocketContext } from "./index";

const NavBar = styled.div`
  height: 32px;
  background-color: #333;
  margin: -28px -16px 20px -16px;
  display: flex;
  align-items: center;
  padding: 0 16px;
  box-sizing: border-box;

  .ms-Button--icon i {
    font-size: 12px;
  }
`;

const StyledRouterLink = styled(RouterLink)`
  display: inline-flex;
  align-items: center;
`;

interface Props {
  baseUrl: string;
}

export default function RocketDetails({ baseUrl }: Props) {
  const imgSubText: ITextStyles = {
    root: {
      color: "#005288",
      fontWeight: FontWeights.semibold,
      textTransform: "uppercase"
    }
  };
  const { rockets } = useRocketContext();
  const match = useRouteMatch<{ rocketId?: string }>(`${baseUrl}/:rocketId`);
  let rocket;
  if (rockets && match) {
    rocket = rockets.find(rocket => rocket.id === match.params.rocketId);
  }
  if (rocket) {
    const {
      name,
      firstFlight,
      description,
      wikipediaUrl,
      costsPerLaunch,
      imgSrc
    } = rocket;
    return (
      <>
        <NavBar>
          <StyledRouterLink to={`${baseUrl}/`}>
            <IconButton
              iconProps={{ iconName: "ChevronLeft" }}
              styles={{
                root: {
                  fontSize: "12px",
                  height: "12px",
                  width: "12px",
                  paddingRight: "1em"
                }
              }}
              title="ChevronLeft"
            />
            <Link styles={{ root: { fontSize: 16 } }}>Home</Link>
          </StyledRouterLink>
        </NavBar>
        <Text
          variant="xxLarge"
          styles={{
            root: { color: "rgb(50, 49, 48)", padding: "0px 0 20px 0" }
          }}
          block
        >
          {name}
        </Text>
        <Image
          height="300px"
          imageFit={ImageFit.cover}
          src={imgSrc}
          width="100%"
        />
        <Stack
          horizontal
          horizontalAlign="space-between"
          styles={{ root: { paddingTop: "10px" } }}
        >
          <Text
            variant="smallPlus"
            styles={imgSubText}
          >{`First Flight: ${firstFlight}`}</Text>
          <Text variant="smallPlus" styles={imgSubText}>
            {`${costsPerLaunch}$ per Launch`}
          </Text>
        </Stack>
        <hr />
        <Text variant="medium" block>
          {description}
        </Text>
        <Link>{wikipediaUrl}</Link>
        <br />
      </>
    );
  }
  return <p>Invalid Rocket ID</p>;
}
