import React from "react";
import {
  Card,
  ICardTokens,
  ICardSectionStyles,
  ICardSectionTokens
} from "@uifabric/react-cards";
import { FontWeights } from "@uifabric/styling";
import {
  Icon,
  IIconStyles,
  Image,
  Persona,
  Stack,
  Text,
  ITextStyles,
  ImageFit
} from "office-ui-fabric-react";

interface Props {
  personaTitle: string;
  personaSubtitle: string;
  headline: string;
  description: string;
  imgSrc: string;
}

export default function MyCard({
  personaTitle,
  personaSubtitle,
  headline,
  description,
  imgSrc
}: Props) {
  const siteTextStyles: ITextStyles = {
    root: {
      color: "#005288",
      fontWeight: FontWeights.semibold
    }
  };
  const descriptionTextStyles: ITextStyles = {
    root: {
      color: "#333333",
      fontWeight: FontWeights.semibold
    }
  };
  const iconStyles: IIconStyles = {
    root: {
      color: "#0078D4",
      fontSize: 16,
      fontWeight: FontWeights.regular
    }
  };
  const footerCardSectionStyles: ICardSectionStyles = {
    root: {
      borderTop: "1px solid #F3F2F1"
    }
  };

  const cardTokens: ICardTokens = { childrenMargin: 12 };
  const footerCardSectionTokens: ICardSectionTokens = {
    padding: "12px 0px 0px"
  };
  return (
    <Card
      styles={{
        root: { maxWidth: "unset", background: "#fff", textDecoration: "none" }
      }}
      aria-label="Clickable vertical card with image bleeding at the center of the card"
      tokens={cardTokens}
    >
      <Card.Item>
        <Persona text={personaTitle} secondaryText={personaSubtitle} />
      </Card.Item>
      <Card.Item fill>
        <Image
          height="300px"
          imageFit={ImageFit.cover}
          src={imgSrc}
          width="100%"
          alt="Placeholder image."
        />
      </Card.Item>
      <Card.Section>
        <Text variant="small" styles={siteTextStyles}>
          {headline}
        </Text>
        <Text styles={descriptionTextStyles}>{description}</Text>
      </Card.Section>
      <Card.Section
        horizontal
        styles={footerCardSectionStyles}
        tokens={footerCardSectionTokens}
      >
        <Icon iconName="Share" styles={iconStyles} />
        <Stack.Item grow={1}>
          <span />
        </Stack.Item>
        <Icon iconName="MoreVertical" styles={iconStyles} />
      </Card.Section>
    </Card>
  );
}
